"use client";

import { motion } from "framer-motion";
import Link from "next/link";

const projects = [
  {
    title: "Creative Agency Website",
    description: "A modern responsive website for a creative agency with branding and animations.",
    image: "/images-s/lee-campbell-DtDlVpy-vvQ-unsplash.jpg",
    link: "/projects/creative-agency",
  },
  {
    title: "E-commerce Platform",
    description: "A scalable and fast e-commerce experience built with Next.js and Shopify.",
    image: "/images-s/lee-campbell-DtDlVpy-vvQ-unsplash.jpg",
    link: "/projects/ecommerce-platform",
  },
  {
    title: "Mobile App Dashboard",
    description: "A mobile-first UI dashboard built for product analytics and tracking.",
    image: "/images-s/lee-campbell-DtDlVpy-vvQ-unsplash.jpg",
    link: "/projects/mobile-dashboard",
  },
];

export default function ViewProjects() {
  return (
    <section className="py-5 px-4 md:px-12">
      <div className="grid grid-row-3 xl:grid-cols-3 gap-5 xl:gap-10 max-w-7xl mx-auto">
        {projects.map((project, index) => (
          <motion.div
            key={index}
            initial={{ opacity: 0, y: 0 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.2, duration: 0.2 }}
            viewport={{ once: true }}
            className="bg-white mx-5 rounded-xl shadow-md overflow-hidden hover:shadow-lg transition"
          >
            {/* Background image div */}
            <div
              className="relative  w-full h-45 xl:h-90 bg-cover bg-center"
              style={{ backgroundImage: `url(${project.image})` }}
            >
              <div className="bg-white absolute h-1/2 xl:h-auto p-2 m-2 bottom-0 rounded-xl opacity-80">
                <div className="">
              <h3 className="text-xl font-semibold">
                {project.title}
              </h3>
              <p className="text-sm mt-2">{project.description}</p>
              <Link href={project.link} className="inline-block 4 text-sm font-medium ">View Project</Link>
            </div>
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
